.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #0009;
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: hidden;
    transition: .1s;
    &.show {
        visibility: visible;
        background: #0009;
        .modal-body{
        transform: translate(0);
        }
    }
}

.modal-body {
    background-color: aliceblue;
    max-width: 550px;
    width: 100%;
    min-height: 220px;
    padding: 28px;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    position: relative;
    transition: .3s;
    transform: translate(100vw);
}

.btn-row {
    margin-top: auto;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 24px;

    input {
    height: 48px;
  }
    &-column {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }
}




